
html{width:100%;height:100%
}
body{margin:0;display:-ms-flexbox;display:flex;-ms-flex-pack:start;justify-content:flex-start
}
#app{color:#2c3e50;width:160px;width:10rem
}
.child-view{position:absolute;width:100%;transition:all 0.6s cubic-bezier(0.55, 0, 0.1, 1)
}
.slide-left-enter,.slide-right-leave-active{opacity:0;transform:translate(50px, 0)
}
.slide-left-leave-active,.slide-right-enter{opacity:0;transform:translate(-50px, 0)
}



